import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../styles/About.css";

const About = () => {
  const navigate = useNavigate();
  const [isHovered, setIsHovered] = useState(false);

  return (
    <section className="about">
      <div className="navbar-background">
        <nav className="navbar">
          <div className="navbar-content">
            <img src="/logo.webp" alt="Logo" className="logo" />
            <ul>
              <li>
                <button onClick={() => navigate("/")}>Strona Gwna</button>
              </li>
              <li>
                <button onClick={() => navigate("/about")}>O Nas</button>
              </li>
              <li
                className="dropdown"
                onMouseEnter={() => setIsHovered(true)}
                onMouseLeave={() => setIsHovered(false)}
              >
                <button
                  className="dropdown-btn"
                  onClick={(e) => {
                    e.stopPropagation(); // Zapobiega zamykaniu dropdownu
                    navigate("/models");
                    setIsHovered(false); // Ukryj menu po klikniciu
                  }}
                >
                  Usugi
                </button>
                {isHovered && (
                  <ul
                    className="dropdown-content"
                    onClick={(e) => e.stopPropagation()}
                  >
                    <li>
                      <button onClick={() => navigate("/trumny-proste")}>
                        Trumny Proste
                      </button>
                    </li>
                    <li>
                      <button onClick={() => navigate("/trumny-francuskie")}>
                        Trumny Francuskie
                      </button>
                    </li>
                    <li>
                      <button onClick={() => navigate("/trumny-nakladane")}>
                        Trumny Nakadane
                      </button>
                    </li>
                    <li>
                      <button onClick={() => navigate("/trumny-samolot")}>
                        Trumny Samolot
                      </button>
                    </li>
                    <li>
                      <button onClick={() => navigate("/trumny-Szstka")}>
                        Trumny Amerykaskie
                      </button>
                    </li>
                    <li>
                      <button onClick={() => navigate("/trumny-Iglo")}>
                        Trumny amane NEW
                      </button>
                    </li>
                  </ul>
                )}
              </li>

              <li>
                <button onClick={() => navigate("/contact")}>Kontakt</button>
              </li>
            </ul>
          </div>
        </nav>
        <div className="about-text">
          <h1>O Nas</h1>
        </div>
      </div>
      <div className="about-content">
        <p>
          Cechy, ktre zdecydowanie wyrniaj firm Kasprzyk na tle innych
          producentw trumien to nowoczesno oraz elastyczno w kontaktach z
          klientami. Wychodzc naprzeciw oczekiwaniom naszych kontrahentw  w
          krtkim czasie realizujemy nie tylko zamwienia na produkty
          standardowe, ale przede wszystkim tak zwane zamwienia nietypowe. S
          to trumny realizowane na specjalne zamwienie klienta, ze wzgldu ich
          parametry i przeznaczenie: uwzgldniajce wiek, wzrost czy wag
          zmarego. Podejmujemy si take przygotowa trumny kremacyjne.
        </p>
        <p>
          Dziki rnorodnoci wytwarzanych trumien jestemy w stanie sprosta
          oczekiwaniom kadego zleceniodawcy, zarwno pod wzgldem wzornictwa,
          jak, ceny oraz wysoko gatunkowych materiaw jak nasze trumny dbowe.
          Jestemy firm, ktra swoje produkty tworzy od samego pocztku do
          samego koca, biorc odpowiedzialno za powstay asortyment.
        </p>
        <img src="/image9.jpg" alt="Trumna" />
        <p>
          Posiadamy bogaty park maszynowy, w skad ktrego wchodz urzdzenia
          tworzce modele, ktrych jestemy jedynym producentem w Polsce, a
          specjalistyczna lakiernia zapewnia wysok jako oferowanych
          produktw.
        </p>
        <p>
          Uwaamy, e kada brana  w tym rwnie brana funeralna  musi
          podlega cigemu rozwojowi i poda za nowociami. Dlatego wanie
          asortyment, ktry chcemy Pastwu zaproponowa, pozwoli Wam zbudowa
          przewag nad konkurencj  przede wszystkim poprzez posiadanie w
          swojej ofercie rnorodnych i nowoczesnych modeli.
        </p>
      </div>
      <footer className="footer" id="contact">
        <p>
          &copy; 2025 Zakad Pogrzebowy Kasprzyk. Wszelkie prawa zastrzeone.
        </p>
        <p>Adres: ul. Przykadowa 1, 00-000 Miasto</p>
        <p>Telefon: +48 123 456 789</p>
        <p>Email: kontakt@kasprzyktrumny.pl</p>
      </footer>
    </section>
  );
};

export default About;
