import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Carousel } from "react-responsive-carousel";
import { motion } from "framer-motion";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "../styles/MainPage.css";

const MainPage = () => {
  const navigate = useNavigate();
  const [isHovered, setIsHovered] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const navbar = document.querySelector(".navbar");
      if (window.scrollY > window.innerHeight) {
        navbar.classList.add("navbar-fixed");
      } else {
        navbar.classList.remove("navbar-fixed");
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToBottom = () => {
    const footer = document.querySelector(".footer");
    footer.scrollIntoView({ behavior: "smooth" });
  };

  const scrollToServices = () => {
    navigate("/models");
  };

  const goToContact = () => {
    navigate("/contact");
  };

  const pageVariants = {
    hidden: { opacity: 0, y: -50 },
    visible: { opacity: 1, y: 0, transition: { duration: 1 } },
  };

  return (
    <motion.section
      className="main-page"
      variants={pageVariants} // Ustawiamy warianty animacji
      initial="hidden" // Pocztkowy stan
      animate="visible" // Stan kocowy po zaadowaniu strony
    >
      <nav className="navbar">
        <div className="navbar-content">
          <img src="/logo.webp" alt="Logo" className="logo" />
          <ul>
            <li>
              <button onClick={() => navigate("/")}>Strona Gwna</button>
            </li>
            <li>
              <button onClick={() => navigate("/about")}>O Nas</button>
            </li>
            <li
              className="dropdown"
              onMouseEnter={() => setIsHovered(true)}
              onMouseLeave={() => setIsHovered(false)}
            >
              <button
                className="dropdown-btn"
                onClick={(e) => {
                  e.stopPropagation(); // Zapobiega zamykaniu dropdownu
                  navigate("/models");
                  setIsHovered(false); // Ukryj menu po klikniciu
                }}
              >
                Usugi
              </button>
              {isHovered && (
                <ul
                  className="dropdown-content"
                  onClick={(e) => e.stopPropagation()}
                >
                  <li>
                    <button onClick={() => navigate("/trumny-proste")}>
                      Trumny Proste
                    </button>
                  </li>
                  <li>
                    <button onClick={() => navigate("/trumny-francuskie")}>
                      Trumny Francuskie
                    </button>
                  </li>
                  <li>
                    <button onClick={() => navigate("/trumny-nakladane")}>
                      Trumny Nakadane
                    </button>
                  </li>
                  <li>
                    <button onClick={() => navigate("/trumny-samolot")}>
                      Trumny Samolot
                    </button>
                  </li>
                  <li>
                    <button onClick={() => navigate("/trumny-Szstka")}>
                      Trumny Amerykaskie
                    </button>
                  </li>
                  <li>
                    <button onClick={() => navigate("/trumny-Iglo")}>
                      Trumny amane NEW
                    </button>
                  </li>
                </ul>
              )}
            </li>
            <li>
              <button onClick={() => navigate("/contact")}>Kontakt</button>
            </li>
          </ul>
        </div>
      </nav>
      <Carousel
        className="carousel"
        showThumbs={false}
        showStatus={false}
        infiniteLoop={true}
        autoPlay={true}
        interval={5000} // Ustawienie czasu przesuwania obrazka na 5 sekund
        renderArrowPrev={(onClickHandler, hasPrev, label) =>
          hasPrev && (
            <button
              type="button"
              onClick={onClickHandler}
              title={label}
              className="arrow arrow-prev"
            >
              &#10094;
            </button>
          )
        }
        renderArrowNext={(onClickHandler, hasNext, label) =>
          hasNext && (
            <button
              type="button"
              onClick={onClickHandler}
              title={label}
              className="arrow arrow-next"
            >
              &#10095;
            </button>
          )
        }
        renderIndicator={(onClickHandler, isSelected, index, label) => {
          if (isSelected) {
            return (
              <li
                className="indicator indicator-selected"
                aria-label={`Selected: ${label} ${index + 1}`}
                title={`Selected: ${label} ${index + 1}`}
              />
            );
          }
          return (
            <li
              className="indicator"
              onClick={onClickHandler}
              onKeyDown={onClickHandler}
              value={index}
              key={index}
              role="button"
              tabIndex={0}
              title={`${label} ${index + 1}`}
              aria-label={`${label} ${index + 1}`}
            />
          );
        }}
      >
        <div>
          <img src="/image1.png" alt="Slajd 1" />
          <div className="carousel-caption">
            <p className="opis">Precyzja w kadym detalu</p>
            <h1 className="opisGlowny">
              Kady z naszych produktw jest wykonany z najwiksz starannoci
            </h1>
            <button className="carousel-button" onClick={scrollToBottom}>
              Przejdz dalej
            </button>
          </div>
        </div>
        <div>
          <img src="/image2.png" alt="Slajd 2" />
          <div className="carousel-caption">
            <p className="opis">Trumny z pajczna</p>
            <h1 className="opisGlowny">
              Produkty wysokiej jakoci w bardzo konkurencyjnych cenach
            </h1>
            <button className="carousel-button" onClick={scrollToServices}>
              Katalog trumie
            </button>
          </div>
        </div>
        <div>
          <img src="/image3.png" alt="Slajd 3" />
          <div className="carousel-caption">
            <p className="opis">On co tam ten</p>
            <h1 className="opisGlowny">
              Trumny firmy Kasprzyk - sztuka w subie zmarym
            </h1>
            <button className="carousel-button" onClick={goToContact}>
              Kontakt
            </button>
          </div>
        </div>
      </Carousel>
      <div className="services" id="services">
        <div className="service">
          <div className="service-text">
            <h4>Trumny Wydrzynw</h4>
            <h1>Producent trumien</h1>
            <p>
              W Wasze rce oddajemy wysokiej jakoci produkty  trumny
              pogrzebowe, ktre charakteryzuj si przede wszystkim
              nowoczesnoci oraz rnorodnoci. W swojej ofercie posiadamy
              bogaty asortyment trumien wykonanych z litego drewna (dbowe,
              sosnowe, topolowe), ktre swym wzornictwem nawizuj do stylu
              woskiego, niemieckiego, amerykaskiego czy te hiszpaskiego.
              Firma Kasprzyk Pajczno to produkcent trumien dbowych i z drewna
              mikkiego, ktre cz w sobie elegancj, precyzj i pasj.
            </p>
          </div>
          <div className="service-image">
            <img src="image4.jpg" alt="Trumny" />
          </div>
        </div>

        <div className="service">
          <div className="service-image">
            <img src="image5.webp" alt="Trumny" />
          </div>
          <div className="service-text">
            <h4>Trumny Wydrzynw</h4>
            <h1>Trumny Dbowe</h1>
            <p>
              Szczeglne miejsce wrd produktw firmy Kasprzyk zajmuj trumny
              dbowe. Odznaczaj si one nie tylko niezwyk elegancj, ale
              przede wszystkim s bardzo trwae, a take odporne na dziaanie
              wilgoci. Trumny wykonane z drewna dbowego poddaj si rwnie w
              atwy sposb obrbce mechanicznej, co pozwala nada im
              niepowtarzalne ksztaty oraz wzbogaci o ciekawe zdobienia. Gama
              kolorystyczna w przypadku trumien dbowych rozpita jest od
              odcieni szarej ci po ciemny brz. Deski z drewna dbowego
              podatne s rwnie na polerowanie, dziki czemu trumny dbowe mog
              nabra dodatkowego blasku. Trumny dbowe zwyko uwaa si za
              szczeglnie szlachetne, dlatego wiele rodzin decyduje si na wybr
              wanie tego typu trumny dla bliskiej osoby. Jej nieco wysza, w
              stosunku do innych rodzajw drewna, cena rekompensowana jest
              wysokimi walorami estetycznymi oraz funkcjonalnymi.
            </p>
            <button
              className="carousel-button"
              onClick={() => navigate("/about")}
            >
              O Nas
            </button>
          </div>
        </div>

        <div className="service">
          <div className="service-text">
            <h4>Trumny Wydrzynw</h4>
            <h1>Producent trumien</h1>
            <p>
              W Wasze rce oddajemy wysokiej jakoci produkty  trumny
              pogrzebowe, ktre charakteryzuj si przede wszystkim
              nowoczesnoci oraz rnorodnoci. W swojej ofercie posiadamy
              bogaty asortyment trumien wykonanych z litego drewna (dbowe,
              sosnowe, topolowe), ktre swym wzornictwem nawizuj do stylu
              woskiego, niemieckiego, amerykaskiego czy te hiszpaskiego.
              Firma Kasprzyk Pajczno to produkcent trumien dbowych i z drewna
              mikkiego, ktre cz w sobie elegancj, precyzj i pasj.
            </p>
            <button
              className="carousel-button"
              onClick={() => navigate("/models")}
            >
              Modele Trumien
            </button>
          </div>
          <div className="service-image">
            <img src="image5.webp" alt="Trumny" />
          </div>
        </div>
      </div>
      <footer className="footer" id="contact">
        <p>
          &copy; 2025 Zakad Pogrzebowy Kasprzyk. Wszelkie prawa zastrzeone.
        </p>
        <p>Adres: ul. Przykadowa 1, 00-000 Miasto</p>
        <p>Telefon: +48 123 456 789</p>
        <p>Email: kontakt@kasprzyktrumny.pl</p>
      </footer>
    </motion.section>
  );
};

export default MainPage;
